	module.exports = {"data":{"markdownRemark":{"html":"<figure style=\"width: 650px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/taisha-1-3fee5e04f9eefa894affe1c25c7e5f80-482fd.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 1200px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 67.68953068592057%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"taisha 1\" title=\"\" src=\"/static/taisha-1-3fee5e04f9eefa894affe1c25c7e5f80-abe1c.png\" srcset=\"/static/taisha-1-3fee5e04f9eefa894affe1c25c7e5f80-10046.png 300w,\n/static/taisha-1-3fee5e04f9eefa894affe1c25c7e5f80-3c4a3.png 600w,\n/static/taisha-1-3fee5e04f9eefa894affe1c25c7e5f80-abe1c.png 1200w,\n/static/taisha-1-3fee5e04f9eefa894affe1c25c7e5f80-482fd.png 1662w\" sizes=\"(max-width: 1200px) 100vw, 1200px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<h2 id=\"span-stylecolorgreen-history-span\"><a href=\"#span-stylecolorgreen-history-span\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><span style=\"color:Green\"> History </span></h2>\n<p><strong>Fushimi Inari-taisha</strong> (伏見稲荷大社) - the head shrine of Inari - is one of the oldest shrines located in Fushimi-ku, Kyoto, Japan.  The god worshiped at the Fushimi Inari-taisha Shrine is the famous Inari-Daimyojin, who responds to the prayers of a prosperous business and bountiful harvest. In addition, Inari-Daimyojin also blesses the peaceful family, traffic safety, sublimation in art.</p>\n<h2 id=\"span-stylecolorroyalblue-symbol-span\"><a href=\"#span-stylecolorroyalblue-symbol-span\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><span style=\"color:royalblue\"> Symbol </span></h2>\n<p>It is home to the famed tunnel of thousands of <span style=\"color:red\"> <b> red torii gates </b> </span> (Senbon Torii), which was appeared as the setting for the movie Memoirs of a Geisha. The torii gates along the entire trail are donations by individuals and companies that you will find their name and the date of the donation carved on the back of each gate. This shrine is definitely one of the most popular sightseeing destinations in Japan.</p>\n<figure style=\"width: 650px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/taisha-2-f4265e91af94651f301f1cc8316c8f45-ceb0d.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 936px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 64.1025641025641%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"taisha 2\" title=\"\" src=\"/static/taisha-2-f4265e91af94651f301f1cc8316c8f45-ceb0d.png\" srcset=\"/static/taisha-2-f4265e91af94651f301f1cc8316c8f45-2a3f9.png 300w,\n/static/taisha-2-f4265e91af94651f301f1cc8316c8f45-23084.png 600w,\n/static/taisha-2-f4265e91af94651f301f1cc8316c8f45-ceb0d.png 936w\" sizes=\"(max-width: 936px) 100vw, 936px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<h2 id=\"span-stylecolororangered-the-foxes-span\"><a href=\"#span-stylecolororangered-the-foxes-span\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><span style=\"color:orangered\"> The Foxes </span></h2>\n<p>In ancient Japan, some myths said that foxes are capable of using magic. That’s why <strong>foxes (kitsune) are regarded as the messengers</strong> so many fox statues often found around the shrine. There are many Inari rice cake shops decorated with foxes. There are also a few restaurants, which serve locally themed dishes such as Inari sushi and <strong>Kitsune Udon</strong> (Fox Udon).</p>\n<figure style=\"width: 600px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/taisha-3-e18b006daa9d2d4bcf36c6cbd66956e9-585e2.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 948px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 62.9746835443038%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"taisha 3\" title=\"\" src=\"/static/taisha-3-e18b006daa9d2d4bcf36c6cbd66956e9-585e2.png\" srcset=\"/static/taisha-3-e18b006daa9d2d4bcf36c6cbd66956e9-a0805.png 300w,\n/static/taisha-3-e18b006daa9d2d4bcf36c6cbd66956e9-a73e7.png 600w,\n/static/taisha-3-e18b006daa9d2d4bcf36c6cbd66956e9-585e2.png 948w\" sizes=\"(max-width: 948px) 100vw, 948px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<p>Before leaving, you can take a walk to the small food street next to the shrine, which has various types of street food like okonomiyaki, takoyaki, and fresh orange juice.</p>\n<figure style=\"width: 650px\" class=\"align-center\">\n  \n  <a class=\"gatsby-resp-image-link\" href=\"/static/taisha-4-15174689b72da8efd9f99dcb8b7d8f2e-b0b6a.png\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 952px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 54.41176470588235%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/png;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"taisha 4\" title=\"\" src=\"/static/taisha-4-15174689b72da8efd9f99dcb8b7d8f2e-b0b6a.png\" srcset=\"/static/taisha-4-15174689b72da8efd9f99dcb8b7d8f2e-2324b.png 300w,\n/static/taisha-4-15174689b72da8efd9f99dcb8b7d8f2e-58e88.png 600w,\n/static/taisha-4-15174689b72da8efd9f99dcb8b7d8f2e-b0b6a.png 952w\" sizes=\"(max-width: 952px) 100vw, 952px\">\n    </span>\n  </span>\n  \n  </a>\n    \n  <figcaption></figcaption>\n</figure>\n<table>\n  <tr>\n\t<th style=\"text-align:center; font-size:25px;color:red; font-weight:bold\" colspan=\"2\"> Fushimi Inari-taisha </th>\n     </tr>\n\t\n  <tr>\n\t<td> <b> Rating </b> </td>\n    <td> ⭐ ⭐ ⭐ ⭐ ⭐ </td>\n      </tr>\n\t\n  <tr>\n\t<td> <b> Address </b> </td>\n    <td> 68 Fukakusa Yabunouchi-cho, Fushimi-ku, Kyoto City 612-0882 </td>\n    </tr>\n\t\n  <tr>\n\t<td> <b> Tel/Fax </b> </td>\n    <td> (075) 641-7331  /   (075) 642-2153 </td>\n   </tr>\n\t\n  <tr>\n\t<td> <b> Website </b> </td>\n    <td> <a href=\"http://inari.jp/en/\" target=\"_blank\"> http://inari.jp/en/ </a> </td>\n   </tr>\n\t\n  <tr>\n\t<td> <b> Fee </b> </td>\n    <td>Free </td>\n   </tr>\n\t \n\t </table>\n<h2 id=\"span-stylecolorblue-how-to-get-there-span\"><a href=\"#span-stylecolorblue-how-to-get-there-span\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a><span style=\"color:blue\"> How to get there </span></h2>\n<p>If you use the <strong>JR Line</strong>, stop at Inari Station - the second station from Kyoto Station along the JR Nara Line (~5 minutes). The shrine can also be reached in a short walk from Fushimi Inari Station along the Keihan Main Line.</p>\n<p>If you go by <strong>city bus</strong>, it will stop at Inari-taisha-mae station, from here walk take 7 minutes. In the temple compound, there is a 170-seat parking lot, dedicated to visitors to the temple.</p>\n<p>This shrine is worth to go if you are planning a trip to Japan. The sceneries are fabulous and so \"Japanese\" that are usually showed on TV. Sure you will get many fantastic photo shoots here. Look at the orange-red color of torii gates, so unique and classic. 💙 Check for detail <a href=\"https://www.youtube.com/watch?v=4ofKe0mqjDI\" target=\"_blank\">here</a>, and <a href=\"http://aquabubu.com/blog/travel/Nara-Deer-Park-in-Japan/\" target=\"_blank\">here</a> for the review of Nara deer park.</p>","timeToRead":3,"excerpt":" History  Fushimi Inari-taisha  (伏見稲荷大社) - the head shrine of Inari - is one of the oldest shrines located in Fushimi-ku, Kyoto, Japan.  The…","frontmatter":{"title":"Japan travel - The head shrine Fushimi Inari-taisha (伏見稲荷大社) of Inari, Kyoto","cover":"/images/taisha.png","date":"2016/10/17","category":"Travel","tags":["Travel","japan","place-to-go","kyoto","du-lich","nhat-ban"]},"fields":{"slug":"/en/Japan-travel-Fushimi Inari-taisha-the-head-shrine-of-Inari-Kyoto/"}}},"pathContext":{"path":"/en/Japan-travel-Fushimi Inari-taisha-the-head-shrine-of-Inari-Kyoto/","langKey":"en"}}

